.inner {
    position: relative;
    width: 100%;
    max-width: 1200px;
	min-width: 300px;
	margin: 0 auto;
    padding: 0 30px;
}

@media (max-width: 768px) {
    .inner {
        padding: 0 20px;
    }
}

@media (max-width: 480px) {
    .inner {
        padding: 0 10px;
    }
}
